import React from 'react';
import {Col, Container, Row} from "react-bootstrap";

interface IProps {
    heading: string;
    icon?: string;
}

const LayoutHeading: React.FC<IProps> = (props) => {
    return (
        <>
            <Container className="mt-3">
                <Row>
                    <Col>
                        <p className="h3 text-success">
                            <i className={`bi ${props.icon}`}></i> {props.heading}</p>
                        <p className="fst-italic">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Atque autem
                            dicta est facilis,
                            fugiat, magni, molestiae officia possimus quidem quo sequi tempora. Animi cupiditate
                            deleniti eaque, eligendi facilis fuga minus optio praesentium quisquam quos sunt veniam
                            voluptate voluptatem? Deleniti, ipsam!</p>
                    </Col>
                </Row>
            </Container>
        </>
    )
};
export default LayoutHeading;