import React, {useState} from 'react';
import MainNavBar from "../../../layout/pages/navbar/MainNavBar";
import LayoutHeading from "../../../layout/components/LayoutHeading";
import {Button, Col, Container, Form, Row} from "react-bootstrap";
import {Link, useNavigate} from "react-router-dom";
import {IUserView} from "../../models/IUserView";
import {AppDispatch, useAppDispatch} from "../../../../redux/store";
import * as userActions from "../../../../redux/users/user.actions";

const UserRegister: React.FC = () => {
    const dispatch: AppDispatch = useAppDispatch();
    const navigate = useNavigate();

    const [validated, setValidated] = useState(false);

    const [user, setUser] = useState<IUserView>({
        username: "",
        email: "",
        password: ""
    });

    const updateInput = (event: React.ChangeEvent<HTMLInputElement | any>) => {
        setUser({
            ...user,
            [event.target.name]: event.target.value
        })
    };

    const handleSubmit = (event: React.FormEvent | any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            dispatch(userActions.registerUserAction({user: user})).then((response: any) => {
                if (response && !response.error) {
                    navigate("/users/login");
                }
            });
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    };

    return (
        <>
            <MainNavBar/>
            <LayoutHeading heading={'Register Here'}/>
            <Container>
                <Row>
                    <Col xs={4}>
                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    pattern={"[a-zA-Z0-9]{4,10}"}
                                    name={'username'}
                                    value={user.username}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Username'}></Form.Control>
                                <Form.Control.Feedback type="invalid">
                                    Please choose a valid username.
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    name={'email'}
                                    value={user.email}
                                    onChange={e => updateInput(e)}
                                    type={'email'} placeholder={'Email'}></Form.Control>
                                <Form.Control.Feedback type="invalid">
                                    Please choose a valid Email.
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    pattern={"(?!.*\\s)(?=.*[A-Z])(?=.*[a-z])(?=.*[0-9])(?=.*[~`!@#$%^&*()--+={}\\[\\]|\\\\:;\"'<>,.?/_]).{6,15}"}
                                    name={'password'}
                                    value={user.password}
                                    onChange={e => updateInput(e)}
                                    type={'password'} placeholder={'Password'}></Form.Control>
                                <Form.Control.Feedback type="invalid">
                                    Please choose a valid Password.
                                </Form.Control.Feedback>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Button type={'submit'} variant={'success'}>Register</Button>
                                <Link to={'/'} className="btn btn-dark ms-2">Cancel</Link>
                            </Form.Group>
                        </Form>
                        <small>Already have an account ?
                            <Link to={'/users/login'}
                                  className="text-decoration-none fw-bold text-success"> Login</Link>
                        </small>
                    </Col>
                </Row>
            </Container>
        </>
    )
};
export default UserRegister;