import React, {useEffect, useState} from 'react';
import MainNavBar from "../../../layout/pages/navbar/MainNavBar";
import LayoutHeading from "../../../layout/components/LayoutHeading";
import {Link, useNavigate, useParams, useSearchParams} from "react-router-dom";
import * as userActions from "../../../../redux/users/user.actions";
import {AppDispatch, RootState, useAppDispatch} from "../../../../redux/store";
import * as userReducer from "../../../../redux/users/user.reducer";
import {useSelector} from "react-redux";
import SpinnerUI from "../../../ui/components/spinner/SpinnerUI";
import {IAddressView} from "../../../addresses/models/IAddressView";
import {Button, Col, Container, Form, Row} from "react-bootstrap";

const EditShippingAddress: React.FC = () => {
    const {addressId} = useParams();
    const [searchParams] = useSearchParams();
    const dispatch: AppDispatch = useAppDispatch();
    const navigate = useNavigate();

    const [validated, setValidated] = useState(false);
    const [address, setAddress] = useState<IAddressView>({
        mobile: "",
        flat: "",
        landmark: "",
        street: "",
        city: "",
        state: "",
        country: "",
        pinCode: ""
    });

    // get address information from redux
    const userReduxState: userReducer.InitialState = useSelector((state: RootState) => {
        return state[userReducer.userFeatureKey];
    });

    const {address: reduxAddress, loading} = userReduxState;

    useEffect(() => {
        dispatch(userActions.getAddressAction());
    }, [addressId]);

    useEffect(() => {
        if (reduxAddress && Object.keys(reduxAddress).length > 0) {
            setAddress({
                mobile: reduxAddress.mobile,
                flat: reduxAddress.flat,
                landmark: reduxAddress.landmark,
                street: reduxAddress.street,
                city: reduxAddress.city,
                state: reduxAddress.state,
                country: reduxAddress.country,
                pinCode: reduxAddress.pinCode,
            })
        }
    }, [reduxAddress])


    const updateInput = (event: React.ChangeEvent<HTMLInputElement | any>) => {
        setAddress({
            ...address,
            [event.target.name]: event.target.value
        })
    };

    const handleSubmit = (event: React.FormEvent | any) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === true) {
            dispatch(userActions.updateAddressAction({
                address: address,
                addressId: addressId
            })).then((response: any) => {
                if (response && !response.error) {
                    navigateBackToPreviousPage();
                }
            });
        }
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        setValidated(true);
    };

    const navigateBackToPreviousPage = () => {
        const page = searchParams.get('router');
        if (page === "checkout") {
            navigate("/carts/checkout");
        }
        if (page === "profile") {
            navigate("/users/profile");
        }
    };

    return (
        <>
            {loading && <SpinnerUI/>}
            <MainNavBar/>
            <LayoutHeading heading={'Edit Shipping Address'}/>
            <Container className="mt-3">
                <Row>
                    <Col xs={4}>
                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.mobile}
                                    name={'mobile'}
                                    onChange={e => updateInput(e)}
                                    type={'number'} placeholder={'Mobile'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.flat}
                                    name={'flat'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Flat'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.landmark}
                                    name={'landmark'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Landmark'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.street}
                                    name={'street'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Street'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.city}
                                    name={'city'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'City'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.state}
                                    name={'state'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'State'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.country}
                                    name={'country'}
                                    onChange={e => updateInput(e)}
                                    type={'text'} placeholder={'Country'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Form.Control
                                    required
                                    value={address.pinCode}
                                    name={'pinCode'}
                                    onChange={e => updateInput(e)}
                                    type={'number'} placeholder={'PinCode'}></Form.Control>
                            </Form.Group>
                            <Form.Group className="mb-2">
                                <Button type={'submit'} variant={'success'}>Update</Button>
                                <Button onClick={navigateBackToPreviousPage} variant={'dark'}
                                        className="ms-2">Cancel</Button>
                            </Form.Group>
                        </Form>
                    </Col>
                </Row>
            </Container>
        </>
    )
};
export default EditShippingAddress;