import React, {useEffect} from 'react';
import MainNavBar from "../../../layout/pages/navbar/MainNavBar";
import LayoutHeading from "../../../layout/components/LayoutHeading";
import {Button, Card, Col, Container, ListGroup, ListGroupItem, Row, Table} from "react-bootstrap";
import * as cartReducer from "../../../../redux/carts/cart.reducer";
import {useSelector} from "react-redux";
import {AppDispatch, RootState, useAppDispatch} from "../../../../redux/store";
import SpinnerUI from "../../../ui/components/spinner/SpinnerUI";
import NoProducts from "../../../ui/components/no-product/NoProducts";
import {CartReduxHelper} from "../../../../redux/carts/CartReduxHelper";
import {
    decrementQuantityAction,
    deleteCartItemAction,
    incrementQuantityAction
} from "../../../../redux/carts/cart.reducer";
import * as cartActions from "../../../../redux/carts/cart.actions";
import {useNavigate} from "react-router-dom";
import {ICartRequestView} from "../../models/ICartRequestView";
import * as userActions from "../../../../redux/users/user.actions";

const CartPage: React.FC = () => {
    const dispatch: AppDispatch = useAppDispatch();
    const navigate = useNavigate();

    // get cart State from redux
    const cartReduxState: cartReducer.InitialState = useSelector((state: RootState) => {
        return state[cartReducer.cartFeatureKey];
    });

    const {loading, cart} = cartReduxState;

    useEffect(() => {
        dispatch(cartActions.getCartInfoAction());
    }, [])


    const clickIncrementQty = (productId: string | undefined) => {
        if (productId) {
            dispatch({
                type: `${incrementQuantityAction}`,
                payload: {
                    productId: productId
                }
            })
        }
    }

    const clickDecrementQty = (productId: string | undefined) => {
        if (productId) {
            dispatch({
                type: `${decrementQuantityAction}`,
                payload: {
                    productId: productId
                }
            })
        }
    }

    const clickDeleteItem = (productId: string | undefined) => {
        if (productId) {
            dispatch({
                type: `${deleteCartItemAction}`,
                payload: {
                    productId: productId
                }
            })
        }
    }

    const clickCheckOutToServer = () => {
        const cartPayload: ICartRequestView = {
            products: cart.products.map(product => {
                return {
                    productObj: product._id,
                    count: product.count.toString(),
                    price: product.price
                }
            }),
            tax: CartReduxHelper.calculateTax(cart.products).toString(),
            total: CartReduxHelper.calculateTotal(cart.products).toString(),
            grandTotal: CartReduxHelper.calculateGrandTotal(cart.products).toString(),
        };
        dispatch(cartActions.createCartAction({cart: cartPayload})).then((response: any) => {
            if (response && !response.error) {
                navigate("/carts/checkout");
            }
        });
    };
    return (
        <>
            {loading && <SpinnerUI/>}
            <MainNavBar/>
            <LayoutHeading heading={'Your Cart'}/>
            {
                cart && cart.products.length > 0 &&

                <Container className="mt-3">
                    <Row>
                        <Col xs={9}>
                            <Card className="shadow-lg">
                                <Card.Header className="bg-success text-white">
                                    <p className="h4">Cart Items</p>
                                </Card.Header>
                                <Card.Body className="bg-light-grey">
                                    <Table striped hover className="text-center">
                                        <thead className='bg-warning'>
                                        <tr>
                                            <th>SNO</th>
                                            <th>Image</th>
                                            <th>Title</th>
                                            <th>Price</th>
                                            <th>Qty</th>
                                            <th>Total</th>
                                            <th>Actions</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {
                                            cart && cart.products && cart.products.map((product, index) => {
                                                return (
                                                    <tr key={product._id}>
                                                        <td>{index + 1}</td>
                                                        <td>
                                                            <img src={product.imageUrl} alt="" width={50}
                                                                 height={50}/>
                                                        </td>
                                                        <td>{product.title}</td>
                                                        <td>&#8377;{product.price}</td>
                                                        <td>
                                                            <i
                                                                className="bi bi-dash-circle-fill me-1"
                                                                onClick={() => clickDecrementQty(product._id)}></i>
                                                            {product.count}
                                                            <i className="bi bi-plus-circle-fill ms-1"
                                                               onClick={() => clickIncrementQty(product._id)}
                                                            ></i>
                                                        </td>
                                                        <td>&#8377;{Number(product.price) * product.count}</td>
                                                        <td>
                                                            <Button variant={'danger'}
                                                                    onClick={() => clickDeleteItem(product._id)}
                                                            >
                                                                <i className="bi bi-trash-fill"></i>
                                                            </Button>
                                                        </td>
                                                    </tr>
                                                )
                                            })
                                        }
                                        </tbody>
                                    </Table>
                                </Card.Body>
                            </Card>
                        </Col>
                        <Col xs={3}>
                            <Card className="shadow-lg">
                                <Card.Header className="bg-success text-white">
                                    <p className="h4">Cart Total : </p>
                                </Card.Header>
                                <Card.Body className="bg-light-grey">
                                    <ListGroup>
                                        <ListGroupItem>Total
                                            : <span
                                                className="fw-bold">
                                                &#8377; {CartReduxHelper.calculateTotal(cart.products).toFixed(2)}</span></ListGroupItem>
                                        <ListGroupItem>Tax : <span className="fw-bold">
                                             &#8377; {CartReduxHelper.calculateTax(cart.products).toFixed(2)}
                                        </span></ListGroupItem>
                                        <ListGroupItem>Grand Total : <span className="fw-bold">
                                             &#8377; {CartReduxHelper.calculateGrandTotal(cart.products).toFixed(2)}
                                        </span></ListGroupItem>
                                    </ListGroup>
                                    <div className="d-grid gap-2 mt-3">
                                        <Button variant='warning' onClick={clickCheckOutToServer}>Checkout</Button>
                                    </div>
                                </Card.Body>
                            </Card>
                        </Col>
                    </Row>
                </Container>
            }
            {
                cart && cart.products.length === 0 &&
                <NoProducts/>
            }
        </>
    )
};
export default CartPage;