import {createSlice, isRejectedWithValue, SerializedError} from "@reduxjs/toolkit";
import {IUserView} from "../../modules/users/models/IUserView";
import {IAddressView} from "../../modules/addresses/models/IAddressView";
import * as userActions from "./user.actions";
import {ToastUtil} from "../../util/ToastUtil";
import {TokenUtil} from "../../util/TokenUtil";

export const userFeatureKey = "userFeature";

export interface InitialState {
    loading: boolean;
    errorMessage: SerializedError;
    user: IUserView;
    address: IAddressView;
    token: string;
    isAuthenticated: boolean;
}

const initialState: InitialState = {
    loading: false,
    errorMessage: {} as SerializedError,
    user: {} as IUserView,
    address: {} as IAddressView,
    token: "",
    isAuthenticated: false
};

export const userSlice = createSlice({
    name: "userSlice",
    initialState: initialState,
    reducers: {
        logOffAction: (state, action) => {
            TokenUtil.deleteTokenFromSession();
            state.token = "";
            state.isAuthenticated = false;
            state.user = {} as IUserView;
            ToastUtil.displayInfoMessage("LogOff is Success");
        }
    },
    extraReducers: (builder) => {
        // register User
        builder.addCase(userActions.registerUserAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.registerUserAction.fulfilled, (state, action) => {
            state.loading = false;
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(userActions.registerUserAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage('Registration is Failed');
            }
        })

        // login user
        builder.addCase(userActions.loginUserAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.loginUserAction.fulfilled, (state, action) => {
            state.loading = false;
            ToastUtil.displaySuccessMessage(action.payload.msg);
            TokenUtil.saveTokenToSession(action.payload.data.token);
            state.token = action.payload.data.token;
            state.isAuthenticated = true;
            state.user = action.payload.data.user;
        }).addCase(userActions.loginUserAction.rejected, (state, action) => {
            state.loading = false;
            TokenUtil.deleteTokenFromSession();
            state.token = "";
            state.isAuthenticated = false;
            state.user = {} as IUserView;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage("Login is Failed");
            }
        })

        // Get user information
        builder.addCase(userActions.getUserInfoAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.getUserInfoAction.fulfilled, (state, action) => {
            state.loading = false;
            state.user = action.payload.data;
            state.isAuthenticated = true;
        }).addCase(userActions.getUserInfoAction.rejected, (state, action) => {
            state.loading = false;
            state.user = {} as IUserView;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage('Get User Information is Failed');
            }
        })

        // Update Profile Picture
        builder.addCase(userActions.updateProfilePictureAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.updateProfilePictureAction.fulfilled, (state, action) => {
            state.loading = false;
            state.user = action.payload.data;
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(userActions.updateProfilePictureAction.rejected, (state, action) => {
            state.loading = false;
            state.user = {} as IUserView;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage('Update Profile picture is Failed');
            }
        })

        // Change Password
        builder.addCase(userActions.changePasswordAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.changePasswordAction.fulfilled, (state, action) => {
            state.loading = false;
            state.user = action.payload.data;
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(userActions.changePasswordAction.rejected, (state, action) => {
            state.loading = false;
            state.user = {} as IUserView;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage('Change Password is Failed');
            }
        })

        // Create Address
        builder.addCase(userActions.createAddressAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.createAddressAction.fulfilled, (state, action) => {
            state.loading = false;
            state.address = action.payload.data;
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(userActions.createAddressAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage('Create Address is Failed');
            }
        })

        // Update Address
        builder.addCase(userActions.updateAddressAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.updateAddressAction.fulfilled, (state, action) => {
            state.loading = false;
            state.address = action.payload.data;
            ToastUtil.displaySuccessMessage(action.payload.msg);
        }).addCase(userActions.updateAddressAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage('Update Address is Failed');
            }
        })

        // Get My Address
        builder.addCase(userActions.getAddressAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.getAddressAction.fulfilled, (state, action) => {
            state.loading = false;
            state.address = action.payload.data;
        }).addCase(userActions.getAddressAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage('Get Address is Failed');
            }
        })

        // Delete My Address
        builder.addCase(userActions.deleteAddressAction.pending, (state, action) => {
            state.loading = true;
        }).addCase(userActions.deleteAddressAction.fulfilled, (state, action) => {
            state.loading = false;
            state.address = {} as IAddressView;
            ToastUtil.displayInfoMessage(action.payload.msg);
        }).addCase(userActions.deleteAddressAction.rejected, (state, action) => {
            state.loading = false;
            if (isRejectedWithValue(action)) {
                state.errorMessage = action.payload
                ToastUtil.displayErrorMessage('Delete Address is Failed');
            }
        })
    }
})
export const {logOffAction} = userSlice.actions;