import axios from "axios";
import {ICartRequestView} from "../models/ICartRequestView";
import {ICartResponseView} from "../models/ICartResponseView";

export class CartService {
    private static serverUrl: string = process.env.REACT_APP_EXPRESS_SERVER_URL ? process.env.REACT_APP_EXPRESS_SERVER_URL : "";

    /**
     * @usage : create a Cart
     * @url : http://localhost:9000/api/carts/
     * @body :products[{productObj, count,price}],total,tax,grandTotal
     * @method : POST
     * @access : PRIVATE
     */
    public static createCart(cart: ICartRequestView): Promise<{ data: { status: string, msg: string, data: ICartResponseView } }> {
        const dataUrl: string = `${this.serverUrl}/api/carts/`;
        return axios.post(dataUrl, cart);
    }

    /**
     * @usage : get Cart Info
     * @url : http://localhost:9000/api/carts/me
     * @body : no-params
     * @method : GET
     * @access : PRIVATE
     */
    public static getCartInfo(): Promise<{ data: { status: string, msg: string, data: ICartResponseView } }> {
        const dataUrl: string = `${this.serverUrl}/api/carts/me`;
        return axios.get(dataUrl);
    }

}